import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzTableQueryParams } from 'ng-zorro-antd/table';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import _ from 'lodash';

import {
  ApproveShopGQL,
  GetShopListGQL,
  GetShopListQuery,
  GetShopListQueryVariables,
  OrderBy,
  ShopStatus,
  UpgradeUserGQL,
} from '../../../../graphql/graphql';
import { BlockShopModalComponent } from '../../components/block-shop-modal/block-shop-modal.component';

@Component({
  selector: 'app-shop-list',
  templateUrl: './shop-list.component.html',
  styleUrls: ['./shop-list.component.scss'],
})
export class ShopListComponent implements OnInit, OnDestroy {
  ShopStatus = ShopStatus;
  destroy$: Subject<boolean> = new Subject();
  shops: GetShopListQuery['shops'] = [];
  shopsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetShopListQuery, GetShopListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  searchForm: FormGroup;
  name?: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private getShopListGQL: GetShopListGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private fb: FormBuilder,
    private approveShopGQL: ApproveShopGQL,
    private upgradeUserGQL: UpgradeUserGQL,
    private modalService: NzModalService,
  ) {
    this.searchForm = this.fb.group({
      name: [null],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.name = paramMap.get('name') || '';

        this.patchFormValue();

        if (this.dataSubscription) {
          this.dataQueryRef?.refetch(this.getVariables());
        } else {
          this.dataQueryRef = this.getShopListGQL.watch(this.getVariables(), {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
          });
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(({ data, loading, error, errors }) => {
              this.loading = loading;
              if (loading) {
                return;
              }

              if (error || errors) {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
                return;
              }

              if (data) {
                this.shops = _.cloneDeep(data.shops);
                this.shopsCount = data.shopsConnection.aggregate.count;
              }
            });
        }
      });
  }

  private getVariables(): GetShopListQueryVariables {
    return {
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      where: {
        name: {
          contains: this.name?.trim() || undefined,
        },
      },
      orderBys: [
        {
          status: OrderBy.Asc,
        },
        {
          updatedAt: OrderBy.Desc,
        },
      ],
    };
  }

  private patchFormValue(): void {
    if (this.name && this.searchForm.controls.name.untouched) {
      this.searchForm.patchValue({
        name: this.name,
      });
    }
  }

  handleQueryParamsChange(params: NzTableQueryParams): void {
    this.router.navigate([], {
      queryParams: {
        page: params.pageIndex,
        size: params.pageSize,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    const searchData = this.searchForm.value;
    const name = searchData.name || undefined;

    this.router.navigate([], {
      queryParams: {
        name,
        page: 1,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  approveShop(shop: GetShopListQuery['shops'][0], index: number): void {
    this.approveShopGQL
      .mutate(
        {
          where: {
            id: shop.id,
          },
        },
        {
          errorPolicy: 'all',
        },
      )
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, errors }) => {
        shop.status = ShopStatus.Approved;
        this.shops = [...this.shops];

        if (errors) {
          this.msgService.error(
            this.translateService.instant('ui.message.error'),
          );
        }

        if (data) {
          this.msgService.success(
            this.translateService.instant('ui.message.success'),
          );
        }
      });
  }

  blockShop(shop: GetShopListQuery['shops'][0], index: number): void {
    this.modalService.create({
      nzContent: BlockShopModalComponent,
      nzComponentParams: {
        email: shop.user.email || undefined,
        shopId: shop.id,
      },
      nzOnOk: () => {
        shop.status = ShopStatus.Blocked;
        this.shops = [...this.shops];
      },
    });
  }

  handleOfficialChange(
    shop: GetShopListQuery['shops'][0],
    isOfficial: boolean,
  ): void {
    this.upgradeUserGQL
      .mutate(
        {
          where: {
            id: shop.user.id,
          },
          isOfficial,
        },
        {
          errorPolicy: 'all',
        },
      )
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ errors, data }) => {
        if (errors) {
          this.msgService.error(
            this.translateService.instant('ui.message.error'),
          );
          shop.user.isOfficial = !isOfficial;
          this.shops = [...this.shops];
        }

        if (data) {
          this.msgService.success(
            this.translateService.instant('ui.message.success'),
          );
        }
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
