import { Injectable } from '@angular/core';
import {
  Router,
  Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot,
} from '@angular/router';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  GetShopDetailsGQL,
  GetShopDetailsQuery,
} from '../../../graphql/graphql';

@Injectable()
export class ShopResolver implements Resolve<GetShopDetailsQuery['shop']> {
  constructor(
    private getShopDetailsGQL: GetShopDetailsGQL,
    private router: Router,
  ) {}

  resolve(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ):
    | GetShopDetailsQuery['shop']
    | Observable<GetShopDetailsQuery['shop']>
    | Promise<GetShopDetailsQuery['shop']>
    | null
    | undefined {
    const id = parseInt(route.paramMap.get('id') || '', 10);
    if (id) {
      return this.getShopDetailsGQL
        .fetch(
          {
            id,
          },
          {
            fetchPolicy: 'network-only',
          },
        )
        .pipe(
          map((result) => {
            if (result.error || result.errors) {
              this.router.navigateByUrl('/exception/500');
              return undefined;
            }

            if (result.data?.shop) {
              return result.data.shop;
            } else {
              this.router.navigateByUrl('/exception/404');
              return undefined;
            }
          }),
        );
    } else {
      this.router.navigateByUrl('/exception/404');
      return undefined;
    }
  }
}
