import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { BlockShopGQL } from '../../../../graphql/graphql';

@Component({
  selector: 'app-block-shop-modal',
  templateUrl: './block-shop-modal.component.html',
  styleUrls: ['./block-shop-modal.component.scss'],
})
export class BlockShopModalComponent implements OnInit, OnDestroy {
  @Input() shopId!: number;
  @Input() email?: string;
  destroy$: Subject<boolean> = new Subject();
  validateForm: FormGroup;
  loading = false;

  constructor(
    public modal: NzModalRef,
    private fb: FormBuilder,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private blockShopGQL: BlockShopGQL,
  ) {
    this.validateForm = this.fb.group({
      reason: [null, Validators.required],
    });
  }

  ngOnInit(): void {}

  submitForm(): void {
    Object.values(this.validateForm.controls).forEach((control) => {
      control.markAsDirty();
      control.updateValueAndValidity();
    });

    if (this.validateForm.invalid) {
      return;
    }

    this.loading = true;
    this.blockShopGQL
      .mutate(
        {
          where: {
            id: this.shopId,
          },
          reason: this.validateForm.controls.reason.value,
        },
        {
          errorPolicy: 'all',
        },
      )
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ errors, data }) => {
        if (data) {
          this.msgService.success(
            this.translateService.instant('ui.message.success'),
          );
          this.modal.triggerOk();
          return;
        }

        if (errors) {
          this.msgService.error(
            this.translateService.instant('ui.message.error'),
          );
        }
      });
  }

  destroyModal(): void {
    this.modal.destroy();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
