import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  BaumannSkinType,
  BlockUserGQL,
  Gender,
  GetUserDetailsGQL,
  GetUserDetailsQuery,
  UpgradeUserGQL,
} from 'src/graphql/graphql';

@Component({
  selector: 'app-user-details',
  templateUrl: './user-details.component.html',
  styleUrls: ['./user-details.component.scss'],
})
export class UserDetailsComponent implements OnInit, OnDestroy {
  selectedKey = 'basic';
  user: GetUserDetailsQuery['user'];
  userId!: string;
  userForm: FormGroup;
  genders = Object.values(Gender);
  skinTypes = Object.values(BaumannSkinType);
  isOfficial = false;
  isBlocked = false;
  destroy$: Subject<boolean> = new Subject();

  constructor(
    private modal: NzModalService,
    private fb: FormBuilder,
    private msg: NzMessageService,
    private route: ActivatedRoute,
    private getUserDetailsGQL: GetUserDetailsGQL,
    private blockUserGQL: BlockUserGQL,
    private upgradeUserGQL: UpgradeUserGQL,
    private translateService: TranslateService,
  ) {
    this.userForm = this.fb.group({
      email: [{ value: null, disabled: true }, Validators.email],
      skinType: [{ value: null, disabled: true }],
      account: [{ value: null, disabled: true }],
      birthYear: [{ value: null, disabled: true }],
      gender: [{ value: null, disabled: true }],
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }

  ngOnInit(): void {
    this.userId = this.route.snapshot.paramMap.get('id') || '1';
    this.getUserDetailsGQL
      .fetch({ id: parseInt(this.userId, 10) })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.user = data.user;
        this.isOfficial = !!data.user?.isOfficial;
        this.isBlocked = !!data.user?.isBlocked;

        this.userForm.patchValue({
          account: data.user?.account,
          email: data.user?.email,
          gender: data.user?.gender,
          birthYear: data.user?.birthYear
            ? new Date().setFullYear(data.user.birthYear)
            : null,
          skinType: data.user?.baumannSkinType,
        });
      });
  }

  changeOfficial(isOfficial: boolean): void {
    this.upgradeUserGQL
      .mutate({
        where: { id: this.user?.id },
        isOfficial: isOfficial,
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, errors }) => {
        if (errors) {
          this.msg.success(this.translateService.instant('ui.message.error'));
        } else {
          this.msg.success(this.translateService.instant('ui.message.success'));
        }
      });
  }

  handleToggleBlock(isBlocked: boolean): void {
    this.modal.confirm({
      nzTitle: this.translateService.instant(
        isBlocked ? 'user.block_confirm' : 'user.unblock_confirm',
      ),
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOnOk: () => {
        this.changeBlock(isBlocked);
      },
      nzCancelText: 'No',
      nzOnCancel: () => {
        this.isBlocked = !isBlocked;
      },
    });
  }

  changeBlock(isBlocked: boolean): void {
    this.blockUserGQL
      .mutate({
        where: {
          id: this.user?.id,
        },
        isBlocked: isBlocked,
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, errors }) => {
        if (errors) {
          this.isBlocked = !isBlocked;
          this.msg.error(this.translateService.instant('ui.message.error'));
        } else {
          this.msg.success(this.translateService.instant('ui.message.success'));
        }
      });
  }

  menuItemClick(key: string): void {
    this.selectedKey = key;
  }
}
