import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { EventEmitter } from 'events';
import cloneDeep from 'lodash/cloneDeep';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzTableQueryParams } from 'ng-zorro-antd/table';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import {
  BlockUserGQL,
  GetUserListGQL,
  GetUserListQuery,
  GetUserListQueryVariables,
  OrderBy,
} from 'src/graphql/graphql';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss'],
})
export class UserListComponent implements OnInit, OnDestroy {
  searchForm!: FormGroup;
  users: GetUserListQuery['users'] = [];
  blockUsersLoading = false;
  loading = false;
  destroy$: Subject<boolean> = new Subject();
  usersCount = 0;
  pageNumber = 1;
  pageSize = 10;
  accountName?: string;
  usersQueryRef:
    | QueryRef<GetUserListQuery, GetUserListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  constructor(
    private modal: NzModalService,
    private message: NzMessageService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private translateService: TranslateService,
    private getUserListGQL: GetUserListGQL,
    private blockUserGQL: BlockUserGQL,
  ) {
    this.searchForm = this.fb.group({
      account: [null],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageNumber = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.accountName = paramMap.get('account') || '';

        this.patchFormValue();

        if (this.dataSubscription) {
          this.usersQueryRef?.refetch(this.getVariables());
        } else {
          this.usersQueryRef = this.getUserListGQL.watch(this.getVariables(), {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
          });
          this.dataSubscription = this.usersQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(({ data, loading, error, errors }) => {
              this.loading = loading;
              if (loading) {
                return;
              }
              if (error || errors) {
                this.message.error('ui.message.can_not_load_data');
                return;
              }
              if (data) {
                this.users = cloneDeep(data.users);
                this.usersCount = data.usersConnection.aggregate.count;
              }
            });
        }
      });
  }

  private patchFormValue(): void {
    if (this.accountName) {
      this.searchForm.patchValue({
        account: this.accountName,
      });
    }
  }

  private getVariables(): GetUserListQueryVariables {
    return {
      first: this.pageSize,
      skip: (this.pageNumber - 1) * this.pageSize,
      where: {
        account: {
          contains: this.accountName?.trim() || undefined,
        },
      },
      orderBy: {
        id: OrderBy.Desc,
      },
    };
  }

  handleQueryParamsChange(params: NzTableQueryParams): void {
    this.router.navigate([], {
      queryParams: {
        page: params.pageIndex,
        size: params.pageSize,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleToggleBlockChange(
    user: GetUserListQuery['users'][0],
    isBlocked: boolean,
  ): void {
    this.modal.confirm({
      nzTitle: this.translateService.instant(
        isBlocked ? 'user.block_confirm' : 'user.unblock_confirm',
      ),
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOnOk: () => {
        this.handleBlockChange(user, isBlocked);
      },
      nzCancelText: 'No',
      nzOnCancel: () => {
        user.isBlocked = !isBlocked;
      },
    });
  }

  handleBlockChange(
    user: GetUserListQuery['users'][0],
    isBlocked: boolean,
  ): void {
    this.blockUserGQL
      .mutate(
        {
          where: {
            id: user.id,
          },
          isBlocked,
        },
        {
          errorPolicy: 'all',
        },
      )
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ errors, data }) => {
        if (errors) {
          user.isBlocked = !isBlocked;
          this.users = [...this.users];
          this.message.error(this.translateService.instant('ui.message.error'));
        }

        if (data) {
          this.message.success(
            this.translateService.instant('ui.message.success'),
          );
        }
      });
  }

  handleSearch(): void {
    const formValues = this.searchForm.value;
    const accountName = formValues?.account;

    this.router.navigate([], {
      queryParams: {
        account: accountName,
        page: 1,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
