import { Location } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalService } from 'ng-zorro-antd/modal';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import XLSX from 'xlsx';

import {
  ApproveShopGQL,
  UploadShopToProductsGQL,
  GetShopDetailsQuery,
  ShopStatus,
  ShopToProductCreateInput,
} from '../../../../graphql/graphql';
import { BlockShopModalComponent } from '../../components/block-shop-modal/block-shop-modal.component';

@Component({
  selector: 'app-shop-details',
  templateUrl: './shop-details.component.html',
  styleUrls: ['./shop-details.component.scss'],
})
export class ShopDetailsComponent implements OnInit, OnDestroy {
  ShopStatus = ShopStatus;
  destroy$: Subject<boolean>;
  shop: GetShopDetailsQuery['shop'];
  uploading = false;
  affiliate = false;

  constructor(
    private route: ActivatedRoute,
    private approveShopGQL: ApproveShopGQL,
    private msgService: NzMessageService,
    private modalService: NzModalService,
    private translateService: TranslateService,
    private location: Location,
    private uploadShopToProductsGQL: UploadShopToProductsGQL,
  ) {
    this.destroy$ = new Subject();
  }

  ngOnInit(): void {
    this.route.data.pipe(takeUntil(this.destroy$)).subscribe(({ shop }) => {
      this.shop = shop;
    });
  }

  approveShop(): void {
    this.approveShopGQL
      .mutate(
        {
          where: {
            id: this.shop!.id,
          },
        },
        {
          errorPolicy: 'all',
        },
      )
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, errors }) => {
        if (errors) {
          this.msgService.error(
            this.translateService.instant('ui.message.error'),
          );
        }

        if (data) {
          this.msgService.success(
            this.translateService.instant('ui.message.success'),
          );
        }
      });
  }

  blockShop(): void {
    this.modalService.create({
      nzContent: BlockShopModalComponent,
      nzComponentParams: {
        email: this.shop!.user.email || undefined,
        shopId: this.shop!.id,
      },
      nzOnOk: () => {
        this.location.back();
      },
    });
  }

  handleProductList = (file: NzUploadFile): boolean => {
    this.uploading = true;
    var reader = new FileReader();
    reader.onload = (e) => {
      var buffer = new Uint8Array(e!.target!.result! as ArrayBufferLike);
      var workbook = XLSX.read(buffer, { type: 'array' });

      const firstSheetName = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[firstSheetName];
      const ref = worksheet['!ref'];
      const cell = ref!.split(':')[1];
      const maxRow = +cell.substring(1);

      const data: Array<ShopToProductCreateInput> = [];
      for (let i = 7; i <= maxRow; i++) {
        if (!worksheet[`A${i}`]?.v || worksheet[`C${i}`]?.v) {
          continue;
        }
        const input: ShopToProductCreateInput = {
          product: {
            connect: {
              id: worksheet[`A${i}`].v,
            },
          },
          shop: {
            connect: {
              id: this.shop!.id,
            },
          },
          price: worksheet[`C${i}`]?.v || 0,
          externalLink: {
            create: [],
          },
        };
        if (worksheet[`E${i}`]?.v) {
          input.externalLink?.create.push({
            title: 'Shopee',
            image: {
              create: {
                url: 'https://d1ip8wajnedch4.cloudfront.net/2021/03/05/ic_shopee.jpg',
                name: 'Shopee',
                width: 60,
                height: 60,
              },
            },
            url: worksheet[`E${i}`].v,
            affiliateUrl: this.affiliate ? worksheet[`E${i}`].v : undefined,
          });
        }
        if (worksheet[`G${i}`]?.v) {
          input.externalLink?.create.push({
            title: 'Lazada',
            image: {
              create: {
                url: 'https://d1ip8wajnedch4.cloudfront.net/2021/03/05/ic_lazada.jpg',
                name: 'Lazada',
                width: 60,
                height: 60,
              },
            },
            url: worksheet[`G${i}`].v,
            affiliateUrl: this.affiliate ? worksheet[`G${i}`].v : undefined,
          });
        }
        data.push(input);
      }
      this.uploadShopToProductsGQL
        .mutate(
          {
            data,
          },
          {
            errorPolicy: 'all',
          },
        )
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ errors, data }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant('shop.upload_s2p_error'),
            );
          }

          if (data) {
            this.msgService.success(
              this.translateService.instant('shop.upload_s2p_success'),
            );
          }
          this.uploading = false;
        });
    };
    reader.readAsArrayBuffer(file as unknown as File);
    return false;
  };

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
